import React, { useState, useEffect,useCallback } from 'react'
import { CartoLayer, CartoSQLLayer, MAP_TYPES, setDefaultCredentials, BASEMAP ,API_VERSIONS,colorBins} from '@deck.gl/carto';
import {PolygonLayer} from '@deck.gl/layers'
import {LinearInterpolator,FlyToInterpolator} from '@deck.gl/core';
import axios from 'axios';
import {render} from 'react-dom';
import {StaticMap} from 'react-map-gl';
import DeckGL from '@deck.gl/react';
import {AmbientLight, PointLight, DirectionalLight, LightingEffect} from '@deck.gl/core';
import {PathStyleExtension} from '@deck.gl/extensions';
// import {WebMercatorViewport,LinearInterpolator} from '@deck.gl/core';


const transitionInterpolator = new FlyToInterpolator();

const INITIAL_VIEW_STATE = {
    latitude: -33.8058087,
    longitude: 150.9872926,
    zoom:16,
    pitch: 60,//60
    bearing: 0,

  };

const credentials = {
  username: 'gsc-admin',
  apiKey: 'default_public'//'fFbU97TNhKdhIO4yYyFjdA'
  }
setDefaultCredentials(credentials);

const MAPBOX_ACCESS_TOKEN = 'pk.eyJ1IjoiYnNuYXlhbiIsImEiOiJja3Ewczd3MW4wN3Z3MnVvNDNsenlzNnFsIn0.f8nk8yrxIeCKOlzIy1b-Pg'

//const transitionInterpolator = new LinearInterpolator();

const getFillColor = (value)=>{
  return value=='Residential'?[255,255,255]:[238, 170, 142]
}

// puting some ligtening effects
const material = {
  ambient: 0.2,
  diffuse: 0.6,
  shininess: 32,
  specularColor: [232, 232, 247]
};

const lightingEffect = new LightingEffect({
  light1: new AmbientLight({
    color: [255, 255, 255],
    intensity: 1.0
  }),
  light2: new DirectionalLight({
    color: [255, 255, 255],
    intensity: 2.0,
    direction: [-1, -2, -3],
    _shadow: true
  })
});

lightingEffect.shadowColor = [0, 0, 0, 0.3];

const theme = {
  buildingColor: [232, 232, 247],
  material,
  effects: [lightingEffect]
};


const Map =()=>{

  const [viewState, updateViewState] = useState(INITIAL_VIEW_STATE);

  const SqlBldg = `
  SELECT the_geom_webmercator,maximum_roof_height as height,zones  FROM whid_gda94_building_polygon_whid where maximum_roof_height >0
  `



  //console.log(domain);

  

  const rotateCamera = useCallback(() => {
    updateViewState(v => ({
      ...v,
      bearing: v.bearing +0.5,
      transitionDuration: 500,
      transitionInterpolator,
      onTransitionEnd: rotateCamera
    }));
  }, []);
 
  // const material  ={
  //   ambient: 0.35,
  //   diffuse: 0.6,
  //   shininess: 32,
  //   specularColor: [30, 30, 30]
  // };



  const bldgLayer = new CartoLayer({
    id:'building',
    type: MAP_TYPES.QUERY,
    data:SqlBldg,
    getFillColor:theme.buildingColor,
    
    // getFillColor: (object)=>{
    //   return [...getFillColor(object.properties.zones,200)]
    // },
    // getFillColor:colorBins({
    //   attr: 'height',
    //   domain: [0, 20, 30, 40, 50, 60, 70],
    //   colors: 'PinkYl'
    // }),
    getLineColor:theme.buildingColor,
    elevationScale:3,
    lineWidthMinPixels: 0,
    pickable: true,
    filled: true,
    extruded: true,
    wireframe: true,
    getElevation: f => {
        return f.properties.height ? f.properties.height : 0;
      },
      // transitions: {
      //   getElevation: {
      //     duration: 1000,
      //     enter: () => [0]
      //   }
      // }
    }
  )
  
  const groundLayer =  new PolygonLayer({
    id: 'ground-layer',
    data: [[[150.01467801500007,-34.337415905999933],[150.01467801500007,-32.996069222999949],[151.34389995600009,-32.996069222999949],[151.34389995600009,-34.337415905999933],[150.01467801500007,-34.337415905999933]]],
    getPolygon: f => f,
    stroked: false,
    getFillColor: [0, 0, 0, 0]
  });

  const districtLayer = new CartoLayer({
    id:'district',
    type: MAP_TYPES.QUERY,
    data: `select the_geom_webmercator, district from "gsc-admin".districtboundary `,
  
    getFillColor: [0,0,0,0],
    getLineColor: [211,211,211],
    lineWidthMinPixels: 1,
    pickable: false,
    
  })

  const precintLayer = new CartoLayer({
    id:'precinct-layer',
    type: MAP_TYPES.QUERY,
    data:'SELECT the_geom_webmercator,description from whid_gda94_westmead_health_precinct',
    lineWidthMinPixels:2,
    getFillColor:[0,0,0,0],
    getLineColor:[0,0,0],
    getDashArray: [3, 2],
  dashJustified: true,
  dashGapPickable: true,
  extensions: [new PathStyleExtension({dash: true})]

  })

  //console.log(color.domain())

  return (
    <div>
     
      <DeckGL
        width="100%"
        height="100%"
        controller={true}
        viewState={viewState}
        layers={[groundLayer,bldgLayer,precintLayer]}
        // getTooltip={getTooltip}
        //onLoad={rotateCamera}
        onViewStateChange={v => updateViewState(v.viewState)}
        getTooltip={ ({ object }) => object && `zones: ${object.properties.zones}`}
        effects ={ theme.effects}
       
      >
        <StaticMap
          reuseMaps
          mapStyle= 'mapbox://styles/mapbox/light-v10'//{BASEMAP.PRISTON}//"mapbox://styles/mapbox/dark-v9"
          mapboxApiAccessToken= {MAPBOX_ACCESS_TOKEN}
          preventStyleDiffing
        />
      </DeckGL>
    </div>
  );



}
;

export default Map;